.section{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    width:250px;
    background-color: palevioletred;
    padding: 0 10px;
    margin: 10px 20px;
}
.sectionHeader{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 10px 0;
    font-size: 14px;
    text-align: left;
}
.sectionHeaderStart{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
}
.rightData{
    display: flex;
    flex-direction: row;
    justify-content: end;
    align-items: end;
}
.sectionBody{
    display: flex;
    flex-direction: column;
}